<template>
  <mt-loadmore
    :topMethod="refresh"
    class="page-container"
    ref="loadmore">
    <div class="my-header">
      <div class="userinfo">
        <div class="avatar">
          <img :src="user.avatar" alt="">
        </div>
        <div class="info-container">
          <span class="username" >{{ user.name }}</span>
          <div class="info-item">手机号：{{ user.phone }}</div>
          <div class="info-item">旺旺号：{{ user.wangwang }}</div>
          <div class="info-item">信用积分：{{ user.score }}</div>
        </div>
      </div>
    </div>

    <div class="nav-container">
      <mt-cell
        v-for="item in myRoutes"
        :to="item.path"
        :is-link="true"
        :key="item.name"
        class="nav-item">
        <div slot="title">
          <i class="nav-item-icon" :class="item.icon" v-bind:style="{ color: item.iconColor }"/>
          <span>{{ item.name }}</span>
        </div>
        <span v-if="item.description"
          class="nav-item-description">
          {{ item.description }}
        </span>
      </mt-cell>
    </div>
  </mt-loadmore>
</template>

<script>
import {
  mapGetters,
} from 'vuex';

export default {
  data() {
    return {
    };
  },
  computed: {
    ...mapGetters([
      'user',
      'myRoutes',
    ]),
  },
  created() {
    this.refresh();
  },
  methods: {
    refresh(id) {
      // eslint-disable-next-line
      console.log('refresh user info');
      if (id) {
        this.$refs.loadmore.onTopLoaded(id);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.nav-container {
  padding-top: 5px;

  .nav-item {
    border-bottom: solid 1px gainsboro;
  }
  .nav-item-icon {
    width: 20px;
    text-align: center;
    padding: 5px;
  }
  .nav-item-description {
    color: red;
    font-size: 14px;
  }
}
</style>

<style lang="scss" scoped>
@import '@/assets/common/css/mixin.scss';
.page-container {
  padding-bottom: 40px;
  width: 100%;
}

.my-header {
  padding: 10px 15px;
  background: -webkit-linear-gradient(left, #eb3c3c, #ff7459);
  background: linear-gradient(90deg, #eb3c3c, #ff7459);
  box-shadow: 0 2px 5px rgba(255, 98, 98, .4);
  position: relative;
  @include flexbox(flex-start, space-between, column, wrap);
  .my-settings {
    width: 100%;
    text-align: right;
    >div {
      @include flexbox(flex-end, center, row, nowrap);
      i {
        width: .65rem;
        height: .65rem;
      }
      .settings {
        display: block;
        // background: url('~jd/images/settings.png') no-repeat;
        background-size: 100%; // background-position: -2.6rem 0!important;
      }
      .msg-icon {
        display: block;
        margin-left: 20px;
        // background: url('~jd/images/searchIcon.png') no-repeat;
        background-size: 600% 100%;
        background-position: -1.3rem 0 !important;
      }
    }
  }
  .userinfo {
    @include flexbox(flex-start,
    flex-start,
    row,
    nowrap);
    .avatar {
      padding: 5px;
      img {
        border: 1px solid hsla(0, 0%, 100%, .4);
        border-radius: 60px;
        box-shadow: 0 2px 10px rgba(0, 0, 0, .15);
        width: 100px;
        height: 100px;
      }
    }
    .info-container {
      @include flexbox(center,
      flex-start,
      column,
      wrap);
      flex: initial;
      padding: 0 15px;
      color: #fff;
      font-size: 14px;
      .info-item {
        margin: 5px 0;
      }
      .username {
        font-size: 16px;
        margin-top: 10px;
        margin-bottom: 5px;
      }
    }
  }
}
</style>



